//typing js
import TypeIt from "typeit-react";

//nextui
import { Button } from "@nextui-org/react";

export default function Hero(){
  return(
    <section className="HeroBackground flex justify-center items-center">
      <div className="flex flex-col justify-center items-center gap-y-4 lg:gap-y-10">
        <TypeIt
          className="font-MorabbaMedium text-3xl lg:text-5xl text-white"
          options={{
          strings: ["طراحی خود را به ما بسپارید"],
          speed: 0.5,
          afterComplete: function (instance) {
            // Remove the cursor
            instance.destroy();
          }
         }}
       />
        <TypeIt
          className="font-MorabbaMedium text-4xl lg:text-[64px] text-MainColor"
          options={{
          strings: ["معین وب"],
          startDelay : 1000,
          afterComplete: function (instance) {
            // Remove the cursor
            instance.destroy();
          }
         }}
       />
      <p data-aos-once="true" data-aos="fade-up" data-aos-duration="1000" data-aos-delay="2000" className="font-PinarSemiBold lg:text-xl text-white w-[360px] md:w-[600px] lg:w-[800px] text-center !leading-10">
       طراحی سایت حرفه‌ای ، هنری است که به کمک آن یک دنیای دیجیتال شگفت‌انگیز را خلق می‌کنیم. این هنر ، ترکیبی است از خلاقیت ، تخصص فنی و فهم عمیق درباره نیازهای کاربران است.تیم طراحی سایت ادستیتو ، با برترین تکنیک‌ها و رویکردهای طراحی ، صفحاتی را خلق میکند که نه تنها زیبا و جذاب هستند ، بلکه به طور همزمان کارایی و سازگاری بالایی با تمام دستگاه‌ها و مرورگرها را نیز دارند.
       </p>
      <Button data-aos-once="true" data-aos="fade-up" data-aos-duration="3000" data-aos-delay="3000" className="bg-gradient-to-bl from-MainColor to-MainColor-50 font-PinarSemiBold text-lg p-6 rounded-full mt-10 text-black">
        مشاهده نمونه کارها
      </Button>
      </div>
    </section>
  );
}