//icons
import { WebDesign02Icon, ComputerPhoneSyncIcon, Rocket01Icon, CustomerService02Icon } from "./Icons"

function Property({icon,title,subtitle}){
  return(
    <div data-aos-once="true" data-aos="flip-right" data-aos-duration="500" data-aos-delay="2000" className="dark:bg-MainColor/10 bg-MainColor-10/30 text-MainColor-50 flex flex-col justify-center items-center gap-4 p-7 rounded-3xl">
      <div className="w-[52px] md:w-[64px] lg:w-[72px]">{icon}</div>
      <span className="font-MorabbaMedium text-lg md:text-2xl text-center">{title}</span>  
      <p className="text-sm lg:text-base text-center">{subtitle}</p>
    </div>
  );
}

export default function OurProperty(){
  const data = {
    title : "چرا گروه معین وب",
    subtitle : "مزایای استفاده از خدمات ما"
  }
  const items = [
    {
       icon : <WebDesign02Icon/>,
       title:"خدمات طراحی",
       subtitle : "لورم ایپسوم متنی تستی جهت نمایش می باشد"
    },
    {
       icon : <Rocket01Icon/>,
       title:"سرور پرسرعت",
       subtitle : "لورم ایپسوم متنی تستی جهت نمایش می باشد"
    },
    {
       icon : <CustomerService02Icon/>,
       title:"خدمات طراحی",
       subtitle : "لورم ایپسوم متنی تستی جهت نمایش می باشد"
    },
    {
        icon : <ComputerPhoneSyncIcon/>,
        title:"طراحی اختصاصی",
        subtitle : "لورم ایپسوم متنی تستی جهت نمایش می باشد"
    },
  ]
  return(
    <section className="BaseContainer flex flex-col justify-center items-center gap-4">
    {/* Title and SubTitle */}
    <span data-aos-once="true" data-aos="fade-up" data-aos-duration="1000" className="font-Morabba text-2xl">{data.subtitle}</span>
    <h2 data-aos-once="true" data-aos="fade-up" data-aos-duration="1000" data-aos-delay="1000" className="text-MainColor font-MorabbaSemiBold text-4xl">{data.title}</h2>
    <div className="grid grid-cols-2 lg:grid-cols-4 w-full gap-4 mt-4">
    {
      items.map(item=> <Property icon={item.icon} title={item.title} subtitle={item.subtitle} />)
    }
    </div>
    </section>
  );
}